.card {
  display: inline-block;
  width: 400px;
  padding: 1em;
  border-radius: 1rem !important;
  margin: 10px;
  background: #ddd;
  text-align: left;
  box-shadow: 0px 5px 20px -10px #111111;
  position: relative;
  transition: 0.4s;
}
.card .multi-button {
  z-index: 10;
  position: absolute;
  top: 2rem;
  right: 2rem;
  border-radius: 100%;
  width: 0rem;
  height: 0rem;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  transition: 0.25s cubic-bezier(0.25, 0, 0, 1);
}
.card .multi-button button {
  display: grid;
  place-items: center;
  position: absolute;
  width: 3rem;
  height: 3rem;
  border: none;
  border-radius: 100%;
  background: rgb(255, 68, 68);
  color: rgb(248, 248, 248);
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  cursor: pointer;
  transition: 0.25s cubic-bezier(0.25, 0, 0, 1);
  box-shadow: 0 0 1rem -0.25rem rgb(255, 68, 68);
}
.card .multi-button button:hover {
  background: rgb(248, 248, 248);
  color: rgb(255, 68, 68)!important;
  box-shadow: 0 0 1rem -0.25rem rgb(248, 248, 248);
}
